<template>
  <div>
    <topMember :index="3"></topMember>
    <div class="box mar fffbg"  style="min-height:500px;">
        <div class="mainH">
            <div class="mainHICO"></div><span class="pull-left">订单管理</span>
        </div>
        <div class="pad">
            <div class="title">订单管理</div>
            <div style="width:650px;margin:0 auto;">
                <div style="width:400px;float:left;margin-right:20px;">
                    <el-input v-model="searchInp" placeholder="请输入内容" class="input-with-select">
                    <el-select v-model="selected" slot="prepend" placeholder="请选择">
                        <el-option label="订单编号" value="1"></el-option>
                        <el-option label="用户账号" value="2"></el-option>
                    </el-select>
                </el-input>
            </div>
            <el-button type="primary" @click="taskList">查询</el-button>
            <el-button @click="daochu" >导出所选订单</el-button>
        </div>
        <el-tabs class="mar" v-model="activeName" type="card">
            <el-tab-pane label="发货任务管理" name="first">
                <div class="">
                    <el-table
                        :data="tableData"
                        border
                        @selection-change="handleSelectionChange"
                        :header-cell-style="{background:'#f7f7f7'}"
                        style="width: 100%"
                    >
                        <el-table-column type="selection" width="55" />
                        <el-table-column prop="id" label="平台任务编号" align="center" width="200" />
                        <el-table-column prop="orderCount" align="center" label="订单数" />
                        <el-table-column align="center" label="支付金额">
                            <template slot-scope="scope">
                                {{scope.row.money/100}} 元
                            </template>
                        </el-table-column>
                        <el-table-column align="center" label="支付时间" >
                          <template slot-scope="scope">
                            {{scope.row.createTime | timeFormater}}
                          </template>
                        </el-table-column>
                        <el-table-column prop="state" align="center" label="任务状态">
                            <template slot-scope="scope">
                                <span v-if="scope.row.state==0">未发货</span>
                                <span v-if="scope.row.state==1">已发货</span>
                            </template>
                        </el-table-column>
                        <el-table-column prop="" label="订单备注">
                          <template slot-scope="scope">
                                <div class="edit">
                                    <span class="edit-icon" @click='taskEditClick(scope.row)'>
                                        <img :src='imgUrl'>
                                    </span>
                                    <span>{{scope.row.remark}}</span>
                                </div>
                            </template> 
                          </el-table-column>      
                        <el-table-column fixed="right" label="操作" align="center" >
                            <template slot-scope="scope">
                                <el-link type="primary" @click="detailForm.taskId=scope.row.id;detailList();" :underline="false">查看详细订单</el-link> 
                                <el-link type="default" @click="plfh" >批量发货</el-link>
                                <el-link v-if="scope.row.state == 0" type="default" @click="rejectRow(scope.row)" >退货</el-link>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>  
                <div class="pageDiv">
                    <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :page-sizes="[10, 20, 30, 40]"
                        :page-size="size"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total"
                    />
                </div>
            </el-tab-pane>
            <el-tab-pane label="详细订单管理" name="second">
                <el-row :gutter="15">
                    <el-col :span="5"><el-input v-model="detailForm.id" placeholder="平台订单号"></el-input></el-col>
                    <el-col :span="5"><el-input v-model="detailForm.mobile" placeholder="收件人手机"></el-input></el-col>
                    <el-col :span="5"><el-input v-model="detailForm.orderNum" placeholder="第三方订单号"></el-input></el-col>
                    <el-col :span="4">
                    <!-- <el-select placeholder="快递公司" value="">
                        <el-option label="圆通快递" value="0"></el-option>
                        <el-option label="顺丰快递" value="1"></el-option>
                    </el-select> -->
                    </el-col>
                    <el-col :span="5"><el-input v-model="detailForm.packNum" placeholder="快递单号"></el-input></el-col>
                </el-row>  
                <el-row class="mar" :gutter="15">
                    <el-col :span="8">
                        <el-date-picker
                            v-model="dateValue"
                            type="daterange"
                            range-separator="至"
                            value-format="yyyy-MM-dd"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                        />
                    </el-col>
                    <el-col :span="4">
                        <el-select v-model="detailForm.state" placeholder="全部包裹状态">
                            <el-option label="全部包裹状态" value=""></el-option>
                            <el-option label="未发货" value="0"></el-option>
                            <el-option label="已发货" value="1"></el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="5">
                        <el-button @click="detailList" type="primary">查询</el-button>
                    </el-col>
                </el-row>
                <div class="mar">
                    <el-table
                        :data="tableData2"
                        border
                        :header-cell-style="{background:'#f7f7f7'}"
                        style="width: 100%"
                    >
                        <el-table-column prop="orderNum" label="第三方订单号" align="center" width="200" />
                        <el-table-column prop="expressCompany" align="center" label="快递" />
                        <el-table-column prop="packNum" align="center" label="快递单号" />
                        <el-table-column prop="userName" align="center" label="收件人姓名" />
                        <el-table-column prop="mobile" align="center" label="收件人手机" />
                        <el-table-column align="center" label="商品费用">
                            <template slot-scope="scope">
                                {{scope.row.price/100}} 元
                            </template>  
                        </el-table-column>
                        <el-table-column align="center" label="快递费用">
                            <template slot-scope="scope">
                                {{scope.row.expressPrice/100}} 元
                            </template>  
                        </el-table-column>
                        <el-table-column prop="createTime" width="200" align="center" label="创建时间" />
                        <el-table-column prop="remark" width="200" align="center" label="备注">
                            <template slot-scope="scope">
                                <div class="edit">
                                    <span class="edit-icon" @click='editClick(scope.row)'>
                                        <img :src='imgUrl'>
                                    </span>
                                    <span>{{scope.row.remark}}</span>
                                </div>
                            </template> 
                        </el-table-column>
                    </el-table>
                </div>  
                <div class="pageDiv">
                    <el-pagination
                        background
                        @size-change="handleSizeChange2"
                        @current-change="handleCurrentChange2"
                        :page-sizes="[10, 20, 30, 40]"
                        :page-size="size2"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total2"
                    />
                </div>  
            </el-tab-pane>
        </el-tabs>
    </div>
    </div>
        <foot></foot>
        <el-dialog
            title="包裹详情"
            :visible.sync="lookdetail"
            width="40%"
        >
            <div>
                <div class="f20" style="margin-top:-20px;margin-bottom:10px;">收件信息</div>
                    收件人姓名：孟维<br/>
                    收件人电话：13299992222<br/>
                    收件人地址：河北省石家庄市梅花镇梅花村<br/>
                    快递状态：已发货<br/>
                    <table class="uptable mar" width="100%">
                        <tr>
                            <th>商品信息</th>
                            <th>商品单价</th>
                            <th>数量</th>
                            <th>单个重量/kg</th>
                        </tr> 
                        <tr>
                            <td>
                            <img class="productImg" src="http://baoyitech.oss-cn-hangzhou.aliyuncs.com/15561018380005c74d472e3f16efd762372eaa2298418">
                            润百洁单包抽纸
                            </td>
                            <td align="center">￥0.79</td>
                            <td align="center">3</td>
                            <td align="center">0.06</td>
                        </tr>                
                        <tr>
                            <th colspan="4">包裹重量：0.01kg&nbsp;&nbsp;&nbsp;&nbsp;
                            费用计算：<strong class="red f20">￥70.55</strong></th>
                        </tr> 
                    </table>  
                </div>
                <div class="mar" style="text-align:center;"><el-button type="primary" @click="lookdetail = false">关闭</el-button>
            </div>
        </el-dialog>
        <orderEdit 
            v-if='editState' :data="rowData" 
            @cancel="editCancle"
            @confirm="editConfrim"
        />
        <taskEdit v-if="taskEditState" :data="taskRowData" @cancel="taskEditCancle"  @confirm="taskEditConfirm"/>
        <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <span>该操作将删除该任务并退还用户消费的费用，您是否确认退货</span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogSure">确 定</el-button>
        </span>
    </el-dialog>
    </div>
</template>

<script>
import foot from './public/foot.vue'
import topMember from './public/topMember.vue'
import orderEdit from './order/orderEdit.vue'
import taskEdit from './order/taskEdit.vue'
import back from '../mixins/back'
import timeFormater from '@/utils/timeFormate'
export default {
  components: {
    foot,
    topMember,
    orderEdit,
    taskEdit
  },
  mixins: [back],
  filters: {
    timeFormater
  },
  data() {
    return {
      input1: '',
      selected:'1',
      searchInp:'',
      activeName: 'first',
      page:1,
      size:10,
      total:0,
      page2:1,
      size2:10,
      total2:0,
      account:'',
      orderNum:'',
      editState: false,
      rowData: null, //当前点击的row
      imgUrl: require('./../assets/images/edit.png'),
      taskEditState: false,
      taskRowData: null,
      detailForm:{
        id:'',
        taskId:'',
        mobile:'',
        orderNum:'',
        packNum:'',
        state:'',
        begin:'',
        end:''
      },
      dateValue:'',
      tableData: [],
      tableData2: [],
      lookdetail:false,
      taskIds:''
    };
  },
  mounted(){
    this.taskList();
  },
  methods:{
    taskList(){
        let that = this;
        if(that.selected=='1'){
            that.orderNum=that.searchInp;
            that.account='';
         }else if(that.selected=='2'){
            that.account=that.searchInp;
            that.orderNum=''
         }
        this.axios
          .get(
            // process.env.API_HOST+"task?&page="+(that.page-1)+'&size='+that.size+'&account='+that.account+'&orderNum='+that.orderNum,
            "/giftmall/task?&page="+(that.page-1)+'&size='+that.size+'&account='+that.account+'&orderNum='+that.orderNum,
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                "domain": this.url,
                "Authorization":sessionStorage.getItem('token')
              }
            }
          )
          .then(function(response) {
            if (response.data.code == 200) {
              for(var i=0;i<response.data.data.list.length;i++){
                  if(response.data.data.list[i].payTime){
                      response.data.data.list[i].payTime=that.gettime(response.data.data.list[i].payTime);
                  }
              }
              that.tableData=response.data.data.list;
              that.total=response.data.data.totalCount;
            } 
          });
    },
    editClick(row){
        this.rowData = row;
        this.editState = !this.editState;
    },
    editCancle(){
        this.editState = false
    },
    editConfrim(){
        this.detailList();
        this.editCancle();
    },
    taskEditClick(row) {
      this.taskRowData = row;
      this.taskEditState = !this.taskEditState
    },
    taskEditCancle() {
      this.taskEditState = false
    },
    taskEditConfirm() {
      this.taskEditState = false
      this.taskList()
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size=val;
      this.taskList();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.page=val;
      this.taskList();
    },
    handleSizeChange2(val) {
      console.log(`每页 ${val} 条`);
      this.size2=val;
      this.detailList();
    },
    handleCurrentChange2(val) {
      console.log(`当前页: ${val}`);
      this.page2=val;
      this.detailList();
    },
    gettime(t){
        var _time=new Date(t);
        var year=_time.getFullYear();//2017
        var month=_time.getMonth()>8?(_time.getMonth()+1):'0'+(_time.getMonth()+1);//7
        var date=_time.getDate()>9?_time.getDate():'0'+_time.getDate();//10
        var hour=_time.getHours()>9?_time.getHours():'0'+_time.getHours();;//10
        var minute=_time.getMinutes()>9?_time.getMinutes():'0'+_time.getMinutes();;//56
        var second=_time.getSeconds()>9?_time.getSeconds():'0'+_time.getSeconds();;//15
        
        return year+"-"+month+"-"+date+"   "+hour+":"+minute+":"+second;//这里自己按自己需要的格式拼接
    },
    detailList(){
        this.detailForm.begin=this.dateValue[0];
        this.detailForm.end=this.dateValue[1];
        this.detailForm.page=this.page2-1;
        this.detailForm.size=this.size2;
      this.activeName='second'
      let that=this;
       this.axios
          .get(
            // process.env.API_HOST+'order?'+that.qs.stringify(that.detailForm),
            '/giftmall/order?'+that.qs.stringify(that.detailForm),
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                // "domain": "taobao1.tianyalei.com",
                "domain": this.url,
                "Authorization":sessionStorage.getItem('token')
              }
            }
          )
          .then(function(response) {
            if (response.data.code == 200) {
              for(var i=0;i<response.data.data.list.length;i++){
                  if(response.data.data.list[i].createTime){
                      response.data.data.list[i].createTime=that.gettime(response.data.data.list[i].createTime);
                  }
              }
              that.tableData2=response.data.data.list;
              that.total2=response.data.data.totalCount;
            } 
          });
    },
      look(){
        this.lookdetail=true;
      },
      plfh(){
        window.open('http://kmd.liangjutong.com/')
      },
      handleSelectionChange(val) {
        console.log(val)
        let arry=[]
        for(var i=0;i<val.length;i++){
            arry.push(val[i].id)
        }
        this.taskIds=arry.join(',');
      },
      daochu(){
        let that=this;
        if(this.taskIds==''){
          that.$message({
              message:"请最少勾选一条数据",
              type: 'warning'
           });
           return
        }
        
        // this.axios.post(process.env.API_HOST+'task/outputExcel',
        this.axios.post('/giftmall/task/outputExcel',
        this.qs.stringify({taskIds:this.taskIds}),{
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                'domain':this.url,
                // 'domain':'taobao1.tianyalei.com',

                "Authorization":sessionStorage.getItem('token')
              }
            })
        .then(function (response) {
            if(response.data.code==200){
               let arry=[];
               for (var i=0;i<response.data.data.length;i++){
                  arry.push({
                      订单编号:response.data.data[i].orderId,
                      客户姓名:response.data.data[i].userName,
                      客户地址:response.data.data[i].address,
                      联系电话:response.data.data[i].mobile,
                      快递公司名称:response.data.data[i].expressCompany,
                      运单号:response.data.data[i].packNum,
                      邮费总价:response.data.data[i].expressMoney/100,
                      货品总价:response.data.data[i].goodsMoney/100
                  })
               }
               that.JSONToExcelConvertor(arry,'订单列表')
            }else{
                that.$message({
                  message: response.data.message,
                  type: 'warning'
                });
            }
          })
      },
      //json数据转excel
     JSONToExcelConvertor(JSONData, FileName) {  
            //先转化json  
            var arrData = typeof JSONData != 'object' ? JSON.parse(JSONData) : JSONData;  
            var excel = '<table border="1"  rull="all" style="border-collapse: collapse">';   
            var row = "<tr>";           
            //设置表头  
            var keys = Object.keys(JSONData[0]);  
            keys.forEach(function (item) {
                row += "<td>" + item + '</td>';
            });     
            //换行  
            excel += row + "</tr>";  
            //设置数据
            for (var i = 0; i < arrData.length; i++) {  
                var row = "<tr>";  
                for (var index in arrData[i]) {  
                    console.log(arrData[i][index]);
                    //var value = arrData[i][index] === "." ? "" : arrData[i][index];  
                    row += '<td style=mso-number-format:"\@">' + arrData[i][index] + '</td>';  
                }  
                excel += row + "</tr>";  
            }  

            excel += "</table>";  

            var excelFile = "<html xmlns:o='urn:schemas-microsoft-com:office:office' xmlns:x='urn:schemas-microsoft-com:office:excel' xmlns='http://www.w3.org/TR/REC-html40'>";  
            excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel; charset=UTF-8">';  
            excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel';  
            excelFile += '; charset=UTF-8">';  
            excelFile += "<head>";  
            excelFile += "<!--[if gte mso 9]>";  
            excelFile += "<xml>";  
            excelFile += "<x:ExcelWorkbook>";  
            excelFile += "<x:ExcelWorksheets>";  
            excelFile += "<x:ExcelWorksheet>";  
            excelFile += "<x:Name>";  
            excelFile += "{worksheet}";  
            excelFile += "</x:Name>";  
            excelFile += "<x:WorksheetOptions>";  
            excelFile += "<x:DisplayGridlines/>";  
            excelFile += "</x:WorksheetOptions>";  
            excelFile += "</x:ExcelWorksheet>";  
            excelFile += "</x:ExcelWorksheets>";  
            excelFile += "</x:ExcelWorkbook>";  
            excelFile += "</xml>";  
            excelFile += "<![endif]-->";  
            excelFile += "</head>";  
            excelFile += "<body>";  
            excelFile += excel;  
            excelFile += "</body>";  
            excelFile += "</html>";  

            var uri = 'data:application/vnd.ms-excel;charset=utf-8,' + encodeURIComponent(excelFile);  

            var link = document.createElement("a");      
            link.href = uri;  

            link.style = "visibility:hidden";  
            link.download = FileName + ".xls";  

            document.body.appendChild(link);  
            link.click();  
            document.body.removeChild(link);  
        }
  }
};
</script>

<style>
.title {
  text-align: center;
  font-size:20px;
  padding:20px;
}
.input-with-select .el-select .el-input {
    width: 100px;
  }
.uptable {
  border-collapse: collapse;
}
.uptable td,.uptable th {
  padding:5px;
  font-size:12px;
  border:1px solid #eaeaea;
}
.uptable th {
  background:#f7f7f7;
} 
.productImg {
  float:left;
  margin-right:10px;
  width:50px;
  height:50px;
}
</style>
<style scoped lang="less">
.edit{
    display: flex;
    align-items: center;
    .edit-icon{
        width: 20px;
        height: 20px;
        cursor: pointer;
        img{
            width: 20px;
            display: block;
        }
    }
}
</style>




