module.exports = {
    data() {
        return {
            scopeRow: null,
            dialogVisible: false
        }
    },
    methods: {
        dialogSure(){
            this.dialogVisible = false;
            this.rejectAjax();
        },
        rejectAjax(){
            let row = this.scopeRow;
            let params = new FormData();
            params.append("taskId", row.id);
            this.axios.post( "/giftmall/tui", params,
                {
                    headers: {
                        "Content-type": "application/x-www-form-urlencoded; charset=utf-8"
                    }
                }
            )
            .then( (res) => {
                if (res.data.code == 200) {
                    this.$message('退货成功');
                    this.taskList();
                }else{
                    this.$message.error(res.data.message)
                }
            });
        },
        rejectRow(row){
            this.scopeRow = row;
            this.dialogVisible = true;
        },
        handleClose() {}
    }
}


